import { Box } from "grommet";
import { FadeOutBox } from "./Part2Screen3/Part2Screen3";

export const ButtonCornerMapBox = ({ isShown }: { isShown: boolean }) => {
  return (
    <FadeOutBox
      width="300px"
      height="160px"
      style={{
        zIndex: 1,
        position: "absolute",
        pointerEvents: "none",
        background:
          "linear-gradient(180deg, rgba(32, 33, 34, 0) 0%, #202122 100%)",
        bottom: 0,
        left: 0,
      }}
      isShown={isShown}
    >
      <MapBoxSVG />
    </FadeOutBox>
  );
};

export const FullScreenMapBox = ({ onClose }: { onClose: () => void }) => {
  return (
    <Box
      flex={false}
      style={{
        width: "100%",
        height: "100%",
      }}
      onClick={onClose}
    >
      <MapBoxSVG />
    </Box>
  );
};

